<template>
	<div id="sign-in" class="full-height">
		<div class="container">
			<auth-header />
			<main>
				<div class="wrapper column-2">
					<div class="column">
						<div class="logo">
							<img src="@/assets/logo.svg" alt />
						</div>
						<div class="input-group">
							<label>
								<div class="prefix">
									<i class="icon icon-auth-user"></i>
								</div>
								<input type="text" placeholder=" " v-model.trim="login" />
								<div class="postfix">
									<v-tooltip top :color="$v.login.$invalid ? 'red' : 'green'">
										<template v-slot:activator="{ on }">
											<i
												class="icon"
												:class="$v.login.$invalid ? 'icon-auth-close color-red' : 'icon-auth-check color-green'"
												v-on="on"
											></i>
										</template>
										<span v-if="!$v.login.$invalid">{{ errors.login.valid }}</span>
										<span v-else-if="!$v.login.required">{{ errors.login.required }}</span>
										<span v-else-if="!$v.login.minLength">{{ errors.login.minLength }}</span>
										<span v-else-if="!$v.login.loginError">{{ errors.login.loginError }}</span>
									</v-tooltip>
								</div>
							</label>
						</div>
						<div class="input-group">
							<label>
								<div class="prefix">
									<i
										class="icon icon-auth-lock pointer"
										:class="{ active: showPassword }"
										@click="toggleShowPassword"
									></i>
								</div>
								<input
									:type="showPassword ? 'text' : 'password'"
									placeholder=" "
									v-model.trim="password"
								/>
								<div class="postfix">
									<v-tooltip top :color="$v.password.$invalid ? 'red' : 'green'">
										<template v-slot:activator="{ on }">
											<i
												class="icon"
												:class="$v.password.$invalid ? 'icon-auth-close color-red' : 'icon-auth-check color-green'"
												v-on="on"
											></i>
										</template>
										<span v-if="!$v.password.$invalid">{{ errors.password.valid }}</span>
										<span v-else-if="!$v.password.required">{{ errors.password.required }}</span>
										<span v-else-if="!$v.password.minLength">{{ errors.password.minLength }}</span>
										<span v-else-if="!$v.password.passwordError">{{ errors.password.passwordError }}</span>
									</v-tooltip>
								</div>
							</label>
						</div>
						<div class="checkbox-group">
							<label>
								<input type="checkbox" v-model="remember" />
								<div class="custom-checkbox"></div>
								<span> </span>
							</label>
						</div>
						<v-btn
							outlined
							class="btn-blue btn-width btn-large"
							@click.end="submitForm"
							:loading="loading"
						>
							<i class="icon icon-auth-play"></i>
							<span>  </span>
						</v-btn>
						<a @click="$router.push({ name: 'reset' })"> ?</a>
					</div>
					<div class="column background register">
						<p>    ?</p>
						<hr />
						<div class="art">
							<img src="@/assets/images/art.png" alt />
						</div>
						<v-btn
							outlined
							class="btn-green btn-width btn-large"
							@click="$router.push({ name: 'sign-up'})"
						>
							<i class="icon icon-auth-user-plus"></i>
							<span> </span>
						</v-btn>
					</div>
				</div>
			</main>
			<auth-footer />
		</div>
	</div>
</template>

<script>
import Header from '@/interfaces/auth/common/Header';
import Footer from '@/interfaces/auth/common/Footer';
import { required, between, minLength } from 'vuelidate/lib/validators';
export default {
	name: 'login',
	components: {
		AuthHeader: Header,
		AuthFooter: Footer
	},
	data: () => {
		return {
			showPassword: false,
			loading: false,
			login: '',
			password: '',
			remember: false,
			errors: {
				login: {
					required: '  ',
					minLength: '  4 ',
					valid: '     ',
					loginError: '    '
				},
				password: {
					required: '  ',
					minLength: '  4 ',
					valid: '     ',
					passwordError: ' '
				}
			},

			/**
			 *    
			 */
			loginError: false,
			passwordError: false
		};
	},
	validations: {
		login: {
			required,
			minLength: minLength(4),
			loginError() {
				return !this.loginError;
			}
		},
		password: {
			required,
			minLength: minLength(4),
			passwordError() {
				return !this.passwordError;
			}
		}
	},
	mounted() {},
	methods: {
		/**
		 * / 
		 */
		toggleShowPassword() {
			this.showPassword = !this.showPassword;
		},

		/**
		 *   
		 */
		submitForm() {
			this.toggleLoading();
			this.$v.$touch();
			this.toggleAuthError();
			!this.$v.$invalid ? this.handleForm() : this.toggleLoading(false);
		},

		/**
		 *    
		 */
		handleForm() {
			const login = this.login;
			const password = this.password;
			const remember = this.remember;

			const data = {
				login,
				password,
				remember
			};

			setTimeout(() => {
				this.toggleAuthError(true);
				this.toggleLoading(false);
			}, 2000);
		},

		/**
		 *  
		 */
		toggleAuthError(state = false) {
			this.loginError = state;
			this.passwordError = state;
		},

		/**
		 *   
		 */
		toggleLoading(state = true) {
			this.loading = state;
		}
	}
};
</script>

<style lang="sass" scoped>
</style>
